import React, {Component, Fragment} from 'react';
import {Form, Input, Radio, Button, Icon, Upload, Divider} from 'antd';
import GraphicMessage from '../../../../common/components/GraphicMessage';

class MenuReplyMessage extends Component {
  render() {
    const {messageInfo, changeMessageInfo, uploadImg, updateCurrentMessage, deleteCurrentMessage, addToList, updateIndex, updateMessageItem} = this.props;
    let MessageContent;
    if (messageInfo.replyType === 0) {
      MessageContent = <Form.Item className="inputtxt" label="">
        <Input.TextArea onChange={e => changeMessageInfo("content", e.target.value)} value={messageInfo.content} autoSize={{minRows: 3, maxRows: 6}} placeholder=''/>
      </Form.Item>
    } else {
      let UploadImg;
      if (messageInfo.picurl) {
        UploadImg = <img style={{width: "100px"}} src={messageInfo.picurl} alt="~"/>
      } else {
        UploadImg = <Button><Icon type="upload"/>  </Button>
      }
      MessageContent = <Fragment>
        <Form.Item label="">
          <Input onChange={e => changeMessageInfo("title", e.target.value)} value={messageInfo.title} placeholder=''/>
        </Form.Item>
        <Form.Item label="">
          <Input onChange={e => changeMessageInfo("url", e.target.value)} value={messageInfo.url} placeholder=''/>
        </Form.Item>
        <Form.Item label="">
          <Input.TextArea onChange={e => changeMessageInfo("description", e.target.value)} value={messageInfo.description} autoSize={{minRows: 3, maxRows: 6}} placeholder=''/>
        </Form.Item>
        <Form.Item label="">
          <Upload name="imageFile" action="/reply/upload" showUploadList={false} onChange={uploadImg}>
            {UploadImg}
          </Upload>
        </Form.Item>
        <Form.Item label=" " colon={false}>
          {updateIndex > -1 ? <div>
            <Button onClick={updateCurrentMessage} type="primary" style={{marginRight: 20}}></Button>
            <Button onClick={deleteCurrentMessage}></Button>
          </div> : <div><Button onClick={addToList}></Button><p className="tips">Tips: 18</p></div>}
        </Form.Item>
        <Divider dashed />
        <div style={{width: "50%", margin: "auto"}}>{messageInfo.articles.length > 0 && <GraphicMessage messageList={messageInfo.articles} updateMessageItem={updateMessageItem} />}</div>
      </Fragment>
    }
    return (
    	<div>
        <Form.Item wrapperCol={{offset: 2}}>
          <Radio.Group value={messageInfo.replyType} onChange={e => changeMessageInfo("replyType", e.target.value)}>
            <Radio.Button value={0}></Radio.Button>
            <Radio.Button value={1}></Radio.Button>
          </Radio.Group>
        </Form.Item>
        {MessageContent}
    	</div>
    )
  }
}

export default MenuReplyMessage;