import React, {Component} from 'react';
import {Input, Card, List, Modal, Form, Select, Radio, Tag, Tooltip, Avatar, Icon} from "antd";
import {connect} from "react-redux";
import {Link} from 'react-router-dom';
import {actionCreators} from './store';
import './index.scss';
import Button from "../../../common/components/Button";

const {Search} = Input;
const {Option} = Select;

class Tuoguan extends Component {

  componentDidMount() {
    this.props.getGroupList();
    this.props.getAllGonghaoList();
  }

  render() {
    const {gonghaoTagList, gonghaoList, searchByName, searchName, changeSearchInput, jieguan, addNewGonghao, openGonghaoModal, gonghaoModal, closeGonghaoModal, gonghaoInfo, updateGonghaoInfo, changeGroup, changeAttribute, changeAbTest, deleteGonghao, group, changeSelectValue, status} = this.props;
    let innerWidth = window.innerWidth > 1440;
    const pagination = {
      pageSize: innerWidth ? 24 : 12
    };
    return (
      <div>
        <div className='container'>
          <Search
            placeholder=''
            className='search-btn'
            value={searchName}
            onChange={changeSearchInput}
            onSearch={searchName => searchByName(searchName)}
          />
          <Select value={group} onChange={value => changeSelectValue("group", value)} style={{width: 150, marginLeft: 10}}>
            <Option value=""></Option>
            <Option value=""></Option>
            {gonghaoTagList.map(item => <Option key={item._id} value={item.name}>{item.name}</Option>)}
          </Select>
          <Select value={status} onChange={value => changeSelectValue("status", value)} style={{width: 100, marginLeft: 10}}>
            <Option value=""></Option>
            <Option value={1}></Option>
            <Option value={-1}></Option>
            <Option value={0}></Option>
            <Option value={-2}></Option>
          </Select>
          <div className="fr btn-wrapper">
            <Button background="success" size="default"
                    buttonContent={<Link to='/admin/gonghao/tuoguan/group'></Link>}/>
            <Button size="default" buttonContent="" onClick={addNewGonghao}/>
          </div>
          <div className='table-wrapper'>
            <List
              grid={{gutter: 16, sm: 4, xxl: 6}}
              dataSource={gonghaoList}
              pagination={pagination}
              renderItem={(item, index) => (
                <List.Item>
                  <Card className="card-item"
                        actions={
                          [
                            <Tooltip title=""><span onClick={() => openGonghaoModal(item._id, item.group, item.attribute, item.ab_test)}></span></Tooltip>,
                            <Tooltip title=""><span className={item.status === -2 ? "" : "disabled-click"}
                                           onClick={() => item.status === -2 ? jieguan(item.code, index) : console.log("")}>{item.status === -2 ? "" : (item.status === -1 ? "" : (item.status === 0 ? "" : ""))}</span></Tooltip>,
                            <Tooltip title={<p><br /></p>}><span onClick={() => deleteGonghao(item._id, index)}></span></Tooltip>
                          ]
                        }>
                    {item.head_img ? <img src={item.head_img} alt=""/> : <Avatar shape="circle" size={40} icon="user" style={{marginRight: 5, marginTop: -20}}/>}
                    <div className="gonghao-info">
                      <p>{item.nick_name}</p>
                      <Tag>{item.group}</Tag>
                    </div>
                  </Card>
                </List.Item>
              )}
            />
          </div>
        </div>
        <Modal visible={gonghaoModal} title="" onCancel={closeGonghaoModal} onOk={() => updateGonghaoInfo(gonghaoInfo)}>
          <Form labelCol={{span: 8}} wrapperCol={{span: 10}}>
            <Form.Item label="" >
              <Select value={gonghaoInfo.group || ""} onChange={changeGroup}>
                <Option value=""></Option>
                {gonghaoTagList.map(item => <Option key={item._id} value={item.name}>{item.name}</Option>)}
              </Select>
            </Form.Item>
            <Form.Item label="">
              <Radio.Group value={gonghaoInfo.attribute} onChange={changeAttribute}>
                <Radio value={0}></Radio>
                <Radio value={1}></Radio>
                <Radio value={2}></Radio>
              </Radio.Group>
            </Form.Item>
            <Form.Item label={<span> <Tooltip title={<span>,<br />()</span>}><Icon className="cursor" type="question-circle" theme="filled" /></Tooltip></span>}>
              <Radio.Group value={gonghaoInfo.ab_test} onChange={changeAbTest}>
                <Radio value={0}></Radio>
                <Radio value={1}></Radio>
                <Radio value={2}></Radio>
              </Radio.Group>
            </Form.Item>
          </Form>
        </Modal>
      </div>
    )
  }
}

const mapState = state => ({
  gonghaoTagList: state.gonghao.tuoguan.gonghaoTagList,
  allGonghaoList: state.gonghao.tuoguan.allGonghaoList,
  gonghaoList: state.gonghao.tuoguan.gonghaoList,
  currentPage: state.gonghao.tuoguan.currentPage,
  searchName: state.gonghao.tuoguan.searchName,
  gonghaoModal: state.gonghao.tuoguan.gonghaoModal,
  gonghaoInfo: state.gonghao.tuoguan.gonghaoInfo,
  group: state.gonghao.tuoguan.group,
  status: state.gonghao.tuoguan.status,
});

const mapDispatch = dispatch => ({
  getGroupList() {
    dispatch(actionCreators.getGroupList())
  },
  getAllGonghaoList() {
    dispatch(actionCreators.getAllGonghaoList())
  },
  changePage(page) {
    dispatch(actionCreators.changePage(page))
  },
  changeSearchInput(e) {
    dispatch(actionCreators.changeSearchInput(e.target.value))
  },
  searchByName(value) {
    dispatch(actionCreators.searchByName(value));
  },
  deleteGonghao(id, index) {
    dispatch(actionCreators.deleteGonghao(id, index));
  },
  jieguan(code, index) {
    dispatch(actionCreators.jieguan(code, index));
  },
  addNewGonghao() {
    dispatch(actionCreators.addNewGonghao());
  },
  openGonghaoModal(id, group, attribute, ab_test) {
    dispatch(actionCreators.openGonghaoModal(id, group, attribute, ab_test));
  },
  closeGonghaoModal() {
    dispatch(actionCreators.closeGonghaoModal());
  },
  updateGonghaoInfo(gonghaoInfo) {
    dispatch(actionCreators.updateGonghaoInfo(gonghaoInfo));
  },
  changeGroup(name) {
    dispatch(actionCreators.changeGroup(name));
  },
  changeAttribute(e) {
    let attribute = e.target.value;
    dispatch(actionCreators.changeAttribute(attribute));
  },
  changeAbTest(e) {
    let ab_test = e.target.value;
    dispatch(actionCreators.changeAbTest(ab_test));
  },
  changeSelectValue(key, value) {
    dispatch(actionCreators.changeSelectValue(key, value));
  },
});

export default connect(mapState, mapDispatch)(Tuoguan);