import axios from "axios";

axios.defaults.timeout = 10000;
//
// axios.defaults.headers = {
//   'X-Requested-With': 'XMLHttpRequest',
//   "Content-Type": "application/json;charset=utf-8"
// };
let userInfo = localStorage.getItem("userInfo")

let account_id = userInfo ? JSON.parse(userInfo)._id  : "";

axios.interceptors.request.use(
  config => {
    if(config.method === "get" || config.method === "delete") {
      if (config.url.indexOf('?') >= 0) {
        config.url += ('&account_id=' + account_id + "&_t=" + new Date().getTime());
      } else {
        config.url += ('?account_id=' + account_id + "&_t=" + new Date().getTime());
      }
    } else if(config.method === "post" || config.method === "put") {
      config.data.account_id = account_id;
      config.data._t = new Date().getTime();
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  }
);

//
axios.interceptors.response.use(
  response => {
    return response;
  },
  err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = "";
          break;
        case 401:
          err.message = "";
          break;
        case 403:
          err.message = "";
          break;
        case 404:
          err.message = ",";
          break;
        case 405:
          err.message = "";
          break;
        case 408:
          err.message = "";
          break;
        case 500:
          err.message = "";
          break;
        case 501:
          err.message = "";
          break;
        case 502:
          err.message = "";
          break;
        case 503:
          err.message = "";
          break;
        case 504:
          err.message = "";
          break;
        case 505:
          err.message = "http";
          break;
        default:
          err.message = `${err.response.status}`;
      }
    } else {
      err.message = "";
    }
    return Promise.resolve(err.response);
  }
);

export default axios;