import React, { Component } from 'react';
import { Breadcrumb } from 'antd';
import { withRouter } from 'react-router'
import './index.scss';

const breadList = {
  '/gonghao': '',

  '/gonghao/tuoguan': '',
  '/gonghao/tuoguan/group': '',

  '/gonghao/tuoguan/error': '',

  '/gonghao/tuoguan/config': '',
  '/gonghao/tuoguan/config/create': '',
  '/gonghao/tuoguan/config/update': '',

  '/gonghao/menu': '',
  '/gonghao/menu/create': '',
  '/gonghao/menu/update': '',

  '/gonghao/reply': '',
  '/gonghao/reply/create': '',
  '/gonghao/reply/update': '',

  '/message': '',

  '/message/kefu': '',
  '/message/kefu/create': '',
  '/message/kefu/update': '',

  '/message/group': '',
  '/message/group/material': '',
  '/message/group/history': '',

  '/message/template': '',
  '/message/template/create': '',
  '/message/template/update': '',

  '/channel': "",
  '/channel/spread': '',
  '/channel/spread/create': '',
  '/channel/spread/update': '',

  '/source': '',
  '/source/group': '',
  '/source/group/create': '',
  '/source/group/update': '',

  '/wechat/qrcode': '',
  '/wechat/qrcode/create': '',
  '/wechat/qrcode/update': '',

  '/account': '',
  '/account/list': '',
  '/account/add': '',
};

class BreadWrapper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pathSnippets: null,
      extraBreadcrumbItems: null,
    }
  }

  getPath = () => {
    this.setState({
      pathSnippets: window.location.pathname.split('/').slice(2)
    });
    this.setState((prev) => {
      return {
        extraBreadcrumbItems: prev.pathSnippets.map((_, index) => {
          let url = `/${prev.pathSnippets.slice(0, index + 1).join('/')}`;
          return (
            <Breadcrumb.Item key={url}>
              {breadList[url]}
            </Breadcrumb.Item>
          );
        })
      }
    })
  };

  UNSAFE_componentWillReceiveProps () {
    this.getPath();
  }

  componentDidMount() {
    this.getPath();
  }

  render() {
    const { extraBreadcrumbItems } = this.state;
    return (
    	<div className='bread-wrapper'>
    		<div className='bread-nav'>
          <Breadcrumb>
            {extraBreadcrumbItems}
          </Breadcrumb>
        </div>
    	</div>
    )
  }

}

export default withRouter(BreadWrapper);